import React, {useState} from 'react'
import { Modal } from 'antd'
import { auth, googleAuthProvider } from '../../firebase'
import { createOrUpdateUser } from '../../functions/auth'
import {useDispatch} from 'react-redux'
import {toast} from 'react-toastify'
import {useHistory} from 'react-router-dom'
import './RegisterComplete.css'

const RegisterWithGoogle = ({isRegisterModalVisible, setIsRegisterModalVisible})=>{
    const dispatch = useDispatch()
    const history = useHistory()
    const initialState={
        firstName:'',
        lastName:'',
        dob:'',
        gender:''
    }
    const [info, setInfo] = useState(initialState)
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')

    const handleInfoChange = (e)=>{
        setInfo({...info, [e.target.name] : e.target.value})
    }

    const handleGoogleLogin = async()=>{
        if(username && info){
        await auth.signInWithPopup(googleAuthProvider).then(async(res)=>{
            if(res.user.emailVerified){
                const user = auth.currentUser
                await user.updatePassword(password)
                let idToken = await user.getIdTokenResult();
                let user_token = idToken.token
                createOrUpdateUser(user_token, res.user.email, info, username).then((result)=>{
                    const {email, username, info, _id} = result.data
                        dispatch({
                            type:'LOGGED_IN_USER',
                            payload:{
                                id:_id,
                                email,
                                username,
                                info,
                                token:user_token
                            }
                        })
                        history.push('/messages')
                        setPassword('')
                        setInfo(initialState)
                        setUsername('')
                }).catch((err)=>{
                    toast.error('Some error occured....Please try again!!')
                    setIsRegisterModalVisible(false)
                })
            }
        }).catch((err)=>{
            toast.error('Some error occured....Please try again!!')
            setIsRegisterModalVisible(false)
        })
    }else{
        toast.error('Please fill the form...Try Again!!')
    }
    }
    return(
        <>
            <Modal visible={isRegisterModalVisible} centered onCancel={()=>{setPassword('');setInfo(initialState);setUsername('') ;setIsRegisterModalVisible(false)}} footer={null} title="Regsitration">
                <div className="reg_comp mx-auto text-center">
                    <input type='text' placeholder='First Name' value={info.firstName} name='firstName' onChange={handleInfoChange}/>
                    <input type='text' placeholder='Last Name' value={info.lastName} name='lastName' onChange={handleInfoChange}/>
                    <input type='text' placeholder='Username' value={username} name='' onChange={(e)=>{setUsername(e.target.value)}}/>
                    <input type='password' value={password} onChange={(e)=>setPassword(e.target.value)} placeholder="Password"/>
                    <input type='date' placeholder='Date Of Birth' value={info.dob} name='dob' onChange={handleInfoChange}/><br/>
                    <select name="gender" value={info.gender} onChange={handleInfoChange}>
                        <option>Gender</option>
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                    </select>
                    <button className="btn btn-primary mt-3 mb-3" onClick={handleGoogleLogin}>Submit</button>
                </div>
            </Modal>
        </>
    )
}

export default RegisterWithGoogle